/* exo-2-100normal - latin */
@font-face {
  font-family: 'Exo 2';
  font-style: normal;
  font-display: swap;
  font-weight: 100;
  src:
    local('Exo 2 Thin '),
    local('Exo 2-Thin'),
    url('./files/exo-2-latin-100.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/exo-2-latin-100.woff') format('woff'); /* Modern Browsers */
}

